.card{
    display: flex;
    flex-direction: column;
    width: 20vw;
    background-color: #dadc9e;
    margin: 2vh;
    border-radius: 10px;
    align-items: center;
}

.card:hover{
    box-shadow: 1px 1px 3px 1px #9a9696;
}

.card:hover .delete_btn{
    visibility: visible;
}

.card:hover .edit_btn{
    visibility: visible;
}


.header,.footer{
    display: flex;
    height: 4vh;
    width: 100%;
    align-items: center;
}

.header{
    border-radius:10px 10px 0 0;
    justify-content: flex-end;
}

.footer{
    border-radius:0 0 10px 10px;
    justify-content: flex-end;
}

.textarea{
    color: #393737;
    width: 90%;
    min-height:30vh;
    overflow-wrap: break-word;
    padding: 1vw;
}


.delete_btn,.edit_btn{
    visibility: hidden;
    background-color: transparent;
    font-size: 14px;
    border: 0;
    padding: 1%;
    margin-left: 1.5%;
    height: fit-content;
    width: fit-content;
    border-radius: 50px;
}

.edit_btn:hover{
    cursor: pointer;
    background-color: #b6ebfa;
}

.delete_btn:hover{
    cursor: pointer;
    background-color: #eca2a2;
}

.timestamp{
    padding: 0 5%;
    font-size: x-small;
    color: #606060;
}

@media (max-width: 710px){
    .card{
        width: 50vw;
    }
    .textarea{
        min-height: 20vh;
    }
}